@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400;700&display=swap);
@-webkit-keyframes titleBackground {
    0%{background-position:0% 92%}
    50%{background-position:100% 9%}
    100%{background-position:0% 92%}
}
@keyframes titleBackground {
    0%{background-position:0% 92%}
    50%{background-position:100% 9%}
    100%{background-position:0% 92%}
}

header {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    color: transparent;
    padding: 10px 7px;
}

header h1 {
    font-weight: 700;
    background: linear-gradient(225deg, #41b093, #a250d9, #4dd8d9, #ee7398);
    -webkit-background-clip: text;
            background-clip: text;
    background-size: 1000% 1000%;
    -webkit-animation: titleBackground 20s ease infinite;
    animation: titleBackground 20s ease infinite;
}

header img {
    height: 3.5em;
    width: auto;
    border-radius: 50%;
    opacity: 0.7;
    transition: all 0.5s ease;
    cursor: pointer;
    margin-top: 0.5em;
}

header img:hover {
    box-shadow: white 0 0 20px;
    transform: scale(1.2);
    opacity: 1;
}

@media screen and (min-width: 500px) {
    h1 {
        font-size: 4em;
    }
}

@media screen and (max-width: 499px) {
    h1 {
        font-size: 3em;
    }
}
@-webkit-keyframes boxClickShadow {
    0% {
        background: linear-gradient(315deg, #dfdfdf 0%, #e9e9e9 100%);
        box-shadow: 
        12px 12px 16px 0 rgba(0, 0, 0, 0.2),
        -8px -8px 10px 0 rgba(255, 255, 255, 0.5);
    }
    50% {
        background: #e0e0e0;
        box-shadow: none;
    }
    100% {
        background: linear-gradient(45deg, #dfdfdf 0%, #e9e9e9 100%);
        box-shadow: inset
        3px 3px 5px 0 rgba(0, 0, 0, 0.2),
        inset
        -2px -2px 3px 0 rgba(255, 255, 255, 0.5);
    }
}

@keyframes boxClickShadow {
    0% {
        background: linear-gradient(315deg, #dfdfdf 0%, #e9e9e9 100%);
        box-shadow: 
        12px 12px 16px 0 rgba(0, 0, 0, 0.2),
        -8px -8px 10px 0 rgba(255, 255, 255, 0.5);
    }
    50% {
        background: #e0e0e0;
        box-shadow: none;
    }
    100% {
        background: linear-gradient(45deg, #dfdfdf 0%, #e9e9e9 100%);
        box-shadow: inset
        3px 3px 5px 0 rgba(0, 0, 0, 0.2),
        inset
        -2px -2px 3px 0 rgba(255, 255, 255, 0.5);
    }
}

.meme-section {
    background: #e0e0e0;
    box-shadow: 
        12px 12px 24px 0 rgba(0, 0, 0, 0.2),
        -12px -12px 24px 0 rgba(255, 255, 255, 0.5);
    border-radius: 7px;
    width: 100%;
    height: 680px;
    margin: 20px auto;
    padding: 9px 16px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.meme-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: auto;
    padding: 20px;
    margin-bottom: 20px;
}

.meme-text {
    width: 100%;
}

.meme-text input[type="text"] {
    padding: 10px 14px;
    background: transparent;
    border: none;
    font-size: 1.4em;
    font-family: 'Montserrat', sans-serif;
    transition: all 0.3s ease;
}

.meme-text input[type="text"]:focus {
    outline: none;
    box-shadow: inset
        3px 3px 5px 0 rgba(0, 0, 0, 0.2),
        inset
        -2px -2px 3px 0 rgba(255, 255, 255, 0.5);
}

.meme-form button {
    padding: 10px 14px;
    display: inline-block;
    width: 200px;
    font-size: 1.3em;
    font-family: 'Montserrat', sans-serif;
    background: #e0e0e0;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
}

.meme-form button:hover {
    transform: scale(1.05);
    background: linear-gradient(315deg, #dfdfdf 0%, #e9e9e9 100%);
    box-shadow: 
        12px 12px 16px 0 rgba(0, 0, 0, 0.2),
        -8px -8px 10px 0 rgba(255, 255, 255, 0.5);
}

.meme-form button:active {
    transform: scale(0.95);
    -webkit-animation: boxClickShadow 0.1s ease forwards;
            animation: boxClickShadow 0.1s ease forwards;
    -webkit-animation-delay: 0.1s;
            animation-delay: 0.1s;
}

.meme-form button:focus {
    outline: none;
}

#bottomInput {
    margin: 1% 0 2% 0;
}

.meme img {
    max-height: 50vh;
    width: auto;
}

.meme h2 {
    color: white;
    text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
    text-transform: uppercase;
    position: absolute;
    font-size: 1.8em;
    white-space: nowrap;
    overflow-wrap: break-word;
    word-wrap: break-word;
}

.meme .text {
    width: 100%;
    display: flex;
    justify-content: center;
}

#topText {
    margin: 10px 0 0 15px;
}

#bottomText {
    margin: -50px 0 0 15px;
}

@media screen and (min-width: 769px) {
    .meme-text input[type="text"] {
        margin: 0 2.5%;
        width: 45%;
    }
}

@media screen and (max-width: 768px) {
    .meme-text input[type="text"] {
        margin: 0;
        width: 100%;
    }
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    min-height: 100vh;
    min-width: 100vw;
    font-family: 'Montserrat', sans-serif;
    background: #e0e0e0;
    padding: 20px 10%;
}
